.skiplink {
    position: absolute;
    top: -10000px;
    width: 1px;
    height: 1px;
    overflow: hidden;
}
.skiplink:focus {
  top: 40px;
  width: auto;
  height: auto;
  background: #000;
  border: 1px solid #000;
  color: #fff;
  font-size: 2rem;
  z-index: 10;
  padding: 10px;
  display: inline-block;
}
.menu {
  display: flex;
}
.main-nav {
  background-image: none;
  color: #fff;
  height: 10rem;
  margin-bottom: -10rem;
  height: 15rem;

  &__container {
    padding: 2.5rem 0;
  }
  
  .wrapper {
    justify-content: space-between;
  }
}

.admin-bar header{
  top: 32px;
}

.sub-menu{
  display: none;
  position: absolute;
}

.menu li:hover, .menu li.active{
  .sub-menu{
    display: block;
  }
}
.menu-button{
  display: none;
  width: 45px;
  height: 31px;
  position: relative;
  top: 1.5rem;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;
  z-index: 999;
}

.menu-button span {
	display: block;
	position: absolute;
	height: 3px;
	width: 100%;
	background: black;
	border-radius: 2px;
	opacity: 1;
	left: 0;
	-webkit-transform: rotate(0deg);
	-moz-transform: rotate(0deg);
	-o-transform: rotate(0deg);
	transform: rotate(0deg);
	-webkit-transition: .25s ease-in-out;
	-moz-transition: .25s ease-in-out;
	-o-transition: .25s ease-in-out;
	transition: .25s ease-in-out;
}

.menu-button span:nth-child(1) {
	top: 0px;
}

.menu-button span:nth-child(2) {
	top: 12px;
}

.menu-button span:nth-child(3) {
	top: 24px;
}

.menu-button.open span:nth-child(1) {
	top: 18px;
	-webkit-transform: rotate(135deg);
	-moz-transform: rotate(135deg);
	-o-transform: rotate(135deg);
	transform: rotate(135deg);
}

.menu-button.open span:nth-child(2) {
	opacity: 0;
	left: -60px;
	display: none;
}

.menu-button.open span:nth-child(3) {
	top: 18px;
	-webkit-transform: rotate(-135deg);
	-moz-transform: rotate(-135deg);
	-o-transform: rotate(-135deg);
	transform: rotate(-135deg);
}

@media (max-width: $md){
  .menu{
    display: none;
  }

  .main-nav .menu>ul{
		flex-direction: column;
	}

  .menu-button{
    display: block;
  }
}

// Start

#menu-main-menu {
  list-style: none;
  padding-top: 1.5rem;

  .menu-item {
    
    @include media-query($medium-up) {
      &:not(:last-child) {
        margin-right: 2rem;
      }
    }

    @include media-query($medium-down) {
      margin: 1.5rem 0;
    }

    & a {
      font-weight: bold;
      background-color: rgba($midnight, .75);
      padding: .8rem 1.6rem;
      color: #FFF;

      &:hover {
        background-color: rgba( $neon-pink, .9 );
      }
    }
  }

  &:active {
    outline: none;
  }
}

.breadcrumb {
  &__list {
      list-style: none;
      padding: 6rem 0 3rem $gutter-horizontal-lg;
      
      & > *:not(:last-child) {
          margin-right: .5rem;
      }
  }

  &__item {
      display: inline-block;
      text-transform: capitalize;
      font-weight: 700;
  }

  .fas {
      color: $neon-pink;
  }
}

.menu-main-menu-container {
  z-index: 999;
}